<template>
  <transition>
    <div class="container">
      <div class="card ">
        <div class="card-image">
          <img src="dist/fundo-background.png">
          <span class="card-title">SSANS App</span>
        </div>
        <div class="card-content">
          <!-- <div class="progress">
            <div class="determinate" v-bind:style="{'width': percent +'%'}"></div>
          </div> -->
          <div class="progress">
            <div class="indeterminate"></div>
          </div>
          <p>Thankyou for using the app.</p>
          <p>You will be redirected in a moment</p>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  name: 'out',
  data: function() {
    return {
      percent: 0
    }
  },
  mounted: function() {
    window.sessionStorage.clear();
    setTimeout(() => {
      this.$router.push('/');
    }, 5000);
    /*setInterval(()=>{
      this.percent += 10;
    },500)*/
  }
}

</script>



