<template>
  <transition>
    <div id="loading" v-show="display">
      <img src="dist/loading.svg" class="responsive-img" alt="loading">
      <!-- <div class="preloader-wrapper big active">
      <div class="spinner-layer spinner-blue">
        <div class="circle-clipper left">
          <div class="circle"></div>
        </div><div class="gap-patch">
          <div class="circle"></div>
        </div><div class="circle-clipper right">
          <div class="circle"></div>
        </div>
      </div>
    </div> -->
      <p>{{message}}</p>
    </div>
  </transition>
</template>
<script>
  export default {
  name: 'loader',
  props: ['display'],
  data: function() {
    return {
        message: 'Loading ... Please wait.'
    }
  },
  mounted: function(){
    
  },
  methods: {
    
  }
}

</script>
<style type="text/css" scoped="">
#loading {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #ccc;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  transition: all 600ms ease-in;
}
#loading img { animation-name: spinner; animation-duration: 2s; animation-direction: normal; animation-timing-function: ease-in-out; animation-iteration-count: infinite; }
#loading p { animation: pulse 1s ease-in-out alternate infinite; }
@keyframes spinner {
  from { transform: rotate(0deg); }
  to {transform: rotate(359deg);}
}
@keyframes pulse {
  from { transform: translateY(10px) ; }
  to { transform: translateY(-5px) ; }
}

</style>



