<template>
  <div class="">
    <h5 class="page-title center-align blue-text "># {{amcdetail.a_code}} </h5>
    <table class="striped">
      <tbody>
        <tr>
          <th>Start Date</th>
          <td class="brown-text">{{amcdetail.amc_s_date}}</td>
        </tr>
        <tr>
          <th>End Date</th>
          <td class="brown-text">{{amcdetail.amc_e_date}}</td>
        </tr>
        <tr>
          <th>Amount (INR)</th>
          <td class="brown-text">{{amcdetail.a_amount}}</td>
        </tr>
        <tr>
          <th>Time Period (Mode)</th>
          <td class="brown-text">{{amcdetail.a_time_period}}</td>
        </tr>
        <tr>
          <th>Branch Location</th>
          <td class="brown-text">{{amcdetail.branch_location}}</td>
        </tr>
        <tr>
          <th>Status</th>
          <td class="brown-text">{{amcdetail.a_status}}</td>
        </tr>
      </tbody>
    </table>
    <h5 class="blue-text">Products</h5>
    <table>
      <thead>
        <tr class="blue lighten-3">
          <th>#</th>
          <th>Name</th>
          <th>Category</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in products">
          <td>{{index+1}}</td>
          <td>{{item.p_name}}</td>
          <td>{{item.prod_cate_name}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script type="text/javascript">
  import {eventBus} from '../main.js';
  import axios from 'axios';
  axios.defaults.baseURL = 'http://wowitprojects.com/ssans_solutions/api/';
    export default {
        name: 'amcdetail',
        data: function() {
            return {
                title: '',
                id: this.$route.params.id,
                amcdetail:'',
                products: ''
            }
        },
        mounted: function() {
          eventBus.$emit('showLoader', false);
          axios.post('customer/view-amc',{"amc_id": this.id })
          .then((response)=>{
            if (response.data.status == 1) {
              // console.log(response.data.amc_details);
              this.amcdetail = response.data.amc_details[0];
              this.products = response.data.products;
            } else {
              alert(response.data.message);
            }
            eventBus.$emit('hideLoader', false);
          }).catch((error)=>{
            alert(error + ". Try again");
            eventBus.$emit('hideLoader', false);
          })
        }
    }
</script>


