<template>
  <transition>
  <div>
    <div class="row detail">
      <div class="col s3"><img src="dist/wow-logo.jpg" class="responsive-img" alt=""></div>
      <div class="col s9">
        <h4 class="page-title blue-text ">{{user.f_name}} {{user.l_name}}</h4>
        <p class="grey-text text-darken-3">{{user.email}}</p>
        <p class="grey-text text-darken-1">{{user.mobile_no}}</p>
        <router-link to="/user" class="black-text">View more +</router-link>
        
      </div>
    </div>
    <div class="">
      <ul class="collection dashboard">
        <li class="collection-item avatar amber lighten-3">
          <i class="material-icons circle orange-text ">folder_shared</i>
          <span class="title orange-text text-darken-4">My AMCs</span>
          <p>List all your AMCs</p>
          <router-link to="/amc" class="secondary-content orange-text "><i class="material-icons">chevron_right</i></router-link>
        </li>
        <li class="collection-item avatar amber lighten-3">
          <i class="material-icons circle orange-text ">description</i>
          <span class="title orange-text text-darken-4">My Services</span>
          <p>List all your Services you are enrolled</p>
          <a href="#!" class="secondary-content orange-text "><i class="material-icons">chevron_right</i></a>
          <router-link to="/service" class="secondary-content orange-text "><i class="material-icons">chevron_right</i></router-link>
        </li>
        <!-- <li class="collection-item avatar amber lighten-3">
                    <i class="material-icons circle orange-text ">account_balance_wallet</i>
                    <span class="title orange-text text-darken-4">My Investments</span>
                    <a href="#!" class="secondary-content orange-text "><i class="material-icons">chevron_right</i></a>
                </li>
                <li class="collection-item avatar amber lighten-3" >
                    <i class="material-icons circle orange-text ">monetization_on</i>
                    <span class="title orange-text text-darken-4">My Loans</span>
                    <a href="#!" class="secondary-content orange-text "><i class="material-icons">chevron_right</i></a>
                </li> -->
      </ul>
    </div>
  </div>
</transition>
</template>
<script>
export default {
  name: 'dash',
  data: function() {
    return {
        user: ''
    }
  },
  mounted: function(){
    this.user = JSON.parse(window.sessionStorage.getItem('user'));
  },
  methods: {
    
  }
}

</script>



