<template>
  <div class="">
    <h5 class="page-title center-align blue-text ">#{{servicedetail.s_code}} </h5>
    <table class="striped">
      <tbody>
        <tr>
          <th>Skill Name</th>
          <td class="brown-text">{{servicedetail.skill_name}}</td>
        </tr>
        <tr>
          <th>Skills</th>
          <td class="brown-text">{{servicedetail.s_skills}}</td>
        </tr>
        <tr>
          <th>Assigned to</th>
          <td class="brown-text">{{servicedetail.s_assigned_to}}</td>
        </tr>
        <tr>
          <th>Service Date</th>
          <td class="brown-text">{{servicedetail.s_date}}</td>
        </tr>
        <tr>
          <th>Amount (INR)</th>
          <td class="brown-text">{{servicedetail.s_charge}}</td>
        </tr>
        <tr>
          <th>Time Period (Hours)</th>
          <td class="brown-text">{{servicedetail.s_hrs}}</td>
        </tr>
        <tr>
          <th>Branch Location</th>
          <td class="brown-text">{{servicedetail.branch_location}}</td>
        </tr>
        <tr>
          <th>Status</th>
          <td class="brown-text">{{servicedetail.s_status}}</td>
        </tr>
        <tr>
          <th>Remark</th>
          <td class="brown-text">{{servicedetail.s_remark}}</td>
        </tr>
      </tbody>
    </table>
    <h5 class="blue-text">Products</h5>
    <table>
      <thead>
        <tr class="blue lighten-3">
          <th>#</th>
          <th>Name</th>
          <th>Category</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in products">
          <td>{{index +1}}</td>
          <td>{{item.p_name}}</td>
          <td>{{item.prod_cate_name}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script type="text/javascript">
  import {eventBus} from '../main.js';
  import axios from 'axios';
  axios.defaults.baseURL = 'http://wowitprojects.com/ssans_solutions/api/';
    export default {
        name: 'servicedetail',
        data: function() {
            return {
                title: '',
                id: this.$route.params.id,
                servicedetail:'',
                products:''
            }
        },
        mounted: function() {
          eventBus.$emit('showLoader', false);
          axios.post('customer/view-service',{"service_id": this.id })
          .then((response)=>{
            if (response.data.status == 1) {
              // console.log(response.data.service_details);
              this.servicedetail = response.data.service_details[0];
              this.products = response.data.products;
            } else {
              alert(response.data.message);
            }
            eventBus.$emit('hideLoader', false);
          }).catch((error)=>{
            alert(error + ". Try again");
            eventBus.$emit('hideLoader', false);
          })
        }
    }
</script>


