<template>
  <div>
    <div class="row detail">
      <div class="col s3">
          <i class="medium material-icons grey-text">verified_user</i>
      </div>
      <div class="col s9">
        <h4 class="page-title blue-text ">{{user.f_name}} {{user.l_name}}</h4>
        <p class="grey-text text-darken-3">{{user.mobile_no}}</p>
        <p class="grey-text text-darken-3">{{user.email}}</p>
        <p class="grey-text text-darken-2">{{user.role}}</p>
      </div>
    </div>
    <div class="">
      <ul class="collection ">
        <li class="collection-item avatar " v-for="(val, key, index) in user">
          <i class="material-icons circle grey lighten-2">adjust</i>
          <span class="title orange-text text-darken-4">{{val}}</span>
          <p class="grey-text">{{key}}</p>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  name: 'user',
  data: function() {
    return {
        user: ''
    }
  },
  mounted: function(){
    this.user = JSON.parse(window.sessionStorage.getItem('user'));
  },
  methods: {
    
  }
}

</script>



