<template>
  <div id="app">
    <nav class="z-depth-0">
      <div class="nav-wrapper orange "><a href="#" class="brand-logo center"><img src="dist/logo.png" v-bind:alt="title"></a>
        <ul id="nav-mobile" class="left ">
          <li><a href="#" class=" sidenav-trigger" data-target="slide-out"><i class="material-icons">menu</i></a></li>
        </ul>
        <ul class="right" v-show="back">
          <li><a href="#"><i class="material-icons">arrow_back</i></a></li>
          <!-- <li><a href="#" v-on:click="closeSide()">x</a></li> -->
        </ul>
      </div>
    </nav>
    <ul id="slide-out" class="sidenav">
      <li>
        <div class="user-view">
          <div class="background">
            <img src="dist/abstract-background.jpg">
          </div>
          <a href="#user"><img class="circle" src="dist/wow-logo.jpg"></a>
          <a href="#name"><span class="white-text name">John Doe</span></a>
          <a href="#email"><span class="white-text email">jdandturk@gmail.com</span></a>
        </div>
      </li>
      <li><router-link to="/dash" class="waves-effect sidenav-close" ><i class="material-icons orange-text">dashboard</i> Dashboard</router-link></li>
      <li><router-link to="/amc" class="waves-effect sidenav-close" ><i class="material-icons orange-text">list</i> My AMCs</router-link></li>
      <li><router-link to="/service" class="waves-effect sidenav-close" ><i class="material-icons orange-text">dashboard</i> My Services</router-link></li>
      <li>
        <div class="divider"></div>
      </li>
      <li><router-link to="/out" class="waves-effect sidenav-close" ><i class="material-icons red-text">exit_to_app</i> Logout</router-link></li>
    </ul>
    <main>
      <router-view></router-view>
      <loader v-bind:display="loader" ></loader>
    </main>
  </div>
</template>
<script>
import {eventBus} from './main.js';
import Loader from './components/Loader.vue';

export default {
  name: 'app',
  data() {
    return {
      title: "ssans",
      msg: 'Welcome to Your Vue.js App',
      instance:'',
      back:false,
      loader: false
    }
  },
  components: {
    'loader': Loader
  },
  created: function() {
    eventBus.$on('showLoader', (data)=>{
      this.loader = data;
      // console.log(data);
    });
    eventBus.$on('hideLoader', (data)=>{
      this.loader = data;
    })
  },
  mounted: function() {
    document.addEventListener('DOMContentLoaded', function() {
        var elems = document.querySelectorAll('.sidenav');
        this.instance = M.Sidenav.init(elems, {
            edge: 'left',
            preventScrolling: true
        });
        // this.instance = M.Sidenav.getInstance(elems);
        // console.log(this.instance[0]);
    });
  },
  methods:{
    closeSide: function () {
      this.instance[0].close();
    }
  }
}

</script>
<style>
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.v-enter { opacity: 0; top: -200px; }
.v-enter-active { opacity: 0.5; top:100px; }
.v-enter-to { opacity: 1; top: 0; }
.v-leave { opacity: 1; top: 00px; }
.v-leave-active { opacity: 0.5; top:100px; }
.v-leave-to { opacity: 0; top: -200px; }
</style>



