<template>
  <div id="loginPage">
    <div class="container">
      <h4 class="page-title blue-text ">{{title}}</h4>
      <p>{{msg}}</p>
    </div>
    <ul id="tabs" class="tabs amber lighten-3">
      <li class="tab col s6"><a class="active" href="#login">Login</a></li>
      <li class="tab col s6"><a href="#register">Register</a></li>
    </ul>
    <div class="tab-content">
      <div id="login">
        <div class="input-field">
          <input type="email" id="email" v-model="loginemail" placeholder="Enter Email ID">
          <label class="active blue-text " for="email">Email ID </label>
        </div>
        <div class="input-field">
          <input type="password" id="password" v-model="loginpass" placeholder="Enter password ">
          <label class="active blue-text " for="password">Password</label>
        </div>
        <div class="input-field center-align">
          <button class="btn btn-large blue" v-on:click="doLogin()">Login</button>
        </div>
      </div>
      <div id="register">
        <div class=" " style="height: 200px; display: flex; align-items: center; flex-direction: column; justify-content: space-around;">
          <h4 ><img src="dist/logo.png"  alt="logo"></h4>
          <h5>Coming soon</h5>
        </div>
        <!-- <div class="input-field ">
          <input id="name" type="text" placeholder="Enter Name">
          <label class="active blue-text " for="name">Name</label>
        </div>
        <div class="input-field">
          <input type="email" id="email2" placeholder="Enter Email ID">
          <label class="active blue-text " for="email2">Email ID</label>
        </div>
        <div class="input-field">
          <input type="text" id="mobile_no" placeholder="Enter Mobile Number">
          <label class="active blue-text " for="mobile_no">Mobile Number</label>
        </div>
        <div class="input-field">
          <input type="password" id="password2" placeholder="Enter password ">
          <label class="active blue-text " for="password2">Password</label>
        </div>
        <div class="input-field">
          <button class="btn btn-block blue">Register</button>
        </div> -->
      </div>
    </div>
  </div>
</template>
<script>
import {eventBus} from '../main.js';
import axios from 'axios';
axios.defaults.baseURL = 'http://wowitprojects.com/ssans_solutions/api/';
export default {
  name: 'login',
  data() {
    return {
      title: "Login/Register",
      msg: "Welcome to SSans Solutions Client app",
      loginemail: 'vishwa_mitra@rediffmail.com',
      loginpass: '123456',
      userdata: '',
      loader: true
    }
  },
  mounted: function() {
    var tabelem = document.getElementById('tabs');
    var instance = M.Tabs.init(tabelem, { swipeable: false });
  },
  methods:{
    doLogin: function() {
      // this.$emit('showLoader',this.loader);
      eventBus.$emit('showLoader', true);
      axios.post('customer/login',{"email":this.loginemail,"password":this.loginpass})
      .then((response)=>{
        // console.log(response);
        // this.userdata = response.data.user;
        window.sessionStorage.setItem('user', JSON.stringify(response.data.user));
        window.sessionStorage.setItem('email', response.data.user.email);
        eventBus.$emit('hideLoader', false);
        this.$router.push('/dash');
      }).catch((error)=>{
        alert(error + ". Try again");
        eventBus.$emit('hideLoader', false);
      });
    }
  }
}

</script>
<style scoped="">

a {
  color: #42b983;
}

</style>



