import Vue from 'vue';
import VueRouter from 'vue-router';
import materialize from 'materialize-css';
import App from './App.vue';
import Login from './components/Login.vue';
import Out from './components/Out.vue';
import Dash from './components/Dash.vue';
import User from './components/User.vue';
import Amclist from './components/Amclist.vue';
import Amcdetail from './components/Amcdetail.vue';
import Servicelist from './components/Servicelist.vue';
import Servicedetail from './components/Servicedetail.vue';
import 'materialize-css/dist/css/materialize.min.css';
import './css/custom.css';

Vue.use(VueRouter);
Vue.use(materialize)

const routes = [
	{ path: '/', component: Login },
	{ path: '/out', component: Out },
	{ path: '/dash', component: Dash },
	{ path: '/user', component: User },
	{ path: '/amc', component: Amclist },
	{ path: '/amc/:id', component: Amcdetail },
	{ path: '/service', component: Servicelist },
	{ path: '/service/:id', component: Servicedetail }
];

const router = new VueRouter({
	routes: routes
});

export const eventBus = new Vue();

new Vue({
  el: '#app',
  router: router,
  materialize: materialize,
  render: h => h(App)
})



