<template>
  <div id="amclist">
    <div class="blue " style="padding: 10px;">
      <h4 class="page-title white-text left" style="margin:2px 0;">{{title}} </h4>
      <!-- <div class="right white-text">Filter by | <i class="material-icons tiny">filter_list</i>All</div> -->
      <div style="clear: both;"></div>
    </div>
    <div class="row">
      <ul class="collection custom">
        <li class="collection-item avatar" v-for="item in services">
          <i class="material-icons circle white blue-text text-lighten-3">settings_applications</i>
          <span class="title orange-text ">{{item.s_code}}</span>
          <div class="row"><span class="col s4 grey-text">Service Date</span><span class="col s6 blue-text ">{{item.s_date}}</span>
            <span class="col s4 grey-text">Charge (INR)</span><span class="col s6 blue-text ">{{item.s_charge}}</span>
          </div>
          <!-- <router-link v-bind:to="/amc/detail/:{{item.a_id}}" class="secondary-content orange-text"><i class="material-icons">chevron_right</i></router-link> -->
          <a href="#" v-bind:id="item.s_id" class="secondary-content orange-text" v-on:click="detail(item.s_id)"><i class="material-icons">chevron_right</i></a>
        </li>
      </ul>
    </div>
  </div>
</template>
<script type="text/javascript">
import { eventBus } from '../main.js';
import axios from 'axios';
axios.defaults.baseURL = 'http://wowitprojects.com/ssans_solutions/api/';
export default {
  name: 'servicelist',
  data: function() {
    return {
      title: 'Service List',
      services: '',
      user: '',
      email: ''
    }
  },
  created: function() {
    // this.user = JSON.parse(window.sessionStorage.getItem('user'));
    this.email = window.sessionStorage.getItem('email');
  },
  mounted: function() {
    axios.post('customer/service-list', { "email": this.email })
      .then((result) => {
        // console.log(result.data.service_list);
        if (result.data.status == 1) {
          this.services = result.data.service_list;
          window.sessionStorage.setItem('services', JSON.stringify(result.data.service_list));
        } else {
          alert("Error in fetching "+this.title);
        }
      })
      .catch((error) => {
        alert(error + ". Try again");
      })
  },
  methods: {
    detail: function(id) {
      this.$router.push('/service/' + id);
    }
  }
}

</script>



